<template>
  <div style="background: rgba(0, 0, 0, 0.36)">
    <Card style="width:100%;background: rgba(0, 0, 0, 0.36);border: none;border-bottom: white 1px solid">
      <Icon type="ios-bulb"/>
      说明
      <span style="color: #ffabfa">菜单管理</span>
    </Card>
    <Tabs style="color:white;font-weight: bold">
      <TabPane label="菜单列表" icon="logo-apple">
        <Card :bordered="false"
              style="width: 100%;padding:0px;background: rgba(0, 0, 0, 0.36);margin-top: 0px;">
          <Button icon="md-add" type="default" size="small" shape="circle" style="float: right"
                  @click="saveOrUpdateuserInfoForm('add',{type:'1'})" ghost>增加
          </Button>
          <Icon size="20" type="md-user"/>
          <Input suffix="ios-search" size="small" placeholder="Enter text" style="width: auto;width: 400px"/>
        </Card>
        <Table ref="table"
               style="background: rgba(0, 0, 0, 0.36);"
               :height="clientHeight"
               :columns="userInfoGrid.columns"
               :data="userInfoGrid.data" showStripe>

          <template slot-scope="{ row }" slot="name">
            <Row>
              <Col span="4" v-if="row.type==2">&nbsp;</Col>
              <Col span="8" v-if="row.type==3">&nbsp;</Col>
              <Col :span="16">
                <div style="color: rgba(13,30,13,0.58);border-radius: 20px"
                     :style="{color:function(){
                                if(row.type==2){return '#2d8cf0'}
                                else if(row.type==3){return 'green'}
                                else{return 'yellow'}
                            }()}">
                  <Icon size="30" :type=row.icon></Icon>&nbsp;&nbsp;
                  <strong>{{ row.name }}</strong>&nbsp;&nbsp;
                </div>
              </Col>
            </Row>
          </template>
          <template slot-scope="{ row }" slot="state">
            <strong>{{$util.localDict.userinfo.state[row.state]}}</strong>
          </template>
          <template slot-scope="{ row }" slot="type">
            <strong>{{$util.localDict.userinfo.type[row.type]}}</strong>
          </template>
          <template slot-scope="{ row,index }" slot="avatar">
            <div style="">
              <img v-if="row.avatar.indexOf('http')==0" :src="row.avatar" width="30" height="30" style="border-radius: 15px;margin-left: 3px"/>
              <Icon v-if="!row.avatar" type="ios-flower" size="35"/>
            </div>
          </template>
          <template slot-scope="{ row,index }" slot="action">
            <div style="text-align: right">
              <Icon size="25" style="color: lightblue" type=md-search @click="showUserView(index)"></Icon>
              <Icon size="25" style="color: green" type=ios-create
                    @click="saveOrUpdateuserInfoForm('update',{type:row.type,id:row.id})"></Icon>
              <Icon size="25" style="color: red" type=ios-trash @click="removeUser(row.id)"></Icon>
            </div>
          </template>
        </Table>
        <div style="margin: 10px;">
          <div style="float: right;">
            <Page style="height: 40px" show-total show-elevator show-sizer
                  :total="userInfoGrid.pageData.total"
                  :current="userInfoGrid.pageData.current"
                  :page-size="userInfoGrid.pageData.size"
                  @on-change="changePageNum"
                  @on-page-size-change="changePageSize"
            />
          </div>
        </div>
      </TabPane>
      <TabPane label="Windows" icon="logo-windows">
      </TabPane>

      <TabPane label="Linux" icon="logo-tux">标签三的内容</TabPane>

    </Tabs>


    <Drawer v-model="userInfoForm.show"
            draggable
            :closable="false"
            :mask-closable="false"
            width=1000>

      <div class="ivu-drawer-header">
        <Icon size="20" type="md-close" class="drawer-close-icon" @click="userInfoForm.show=false"/>
        <div class="ivu-drawer-header-inner">{{userInfoForm.title}}</div>
      </div>

      <Card :bordered="false" :style="{height:(clientHeight+220)+'px'}" >
        <Form ref="userInfoForm" :model="userInfoForm.data"
              :rules="userInfoForm.validate" :label-width="80">
          <FormItem label="图像" label-position="left" prop="avatar">
            <Input class="" v-model="userInfoForm.data.avatar"
                   placeholder=""/>
          </FormItem>
          <FormItem label="用户名" label-position="left" prop="username">
            <Input class="" v-model="userInfoForm.data.username"
                   placeholder=""/>
          </FormItem>
          <FormItem label="姓名" label-position="left" prop="name">
            <Input class="" v-model="userInfoForm.data.name"
                   placeholder=""/>
          </FormItem>
          <FormItem label="生日" label-position="left" prop="birthday">
            <Input class="" v-model="userInfoForm.data.birthday"
                   placeholder=""/>
          </FormItem>
          <FormItem label="性别" label-position="left" prop="sex">
            <Input class="" v-model="userInfoForm.data.sex"
                   placeholder=""/>
          </FormItem>
          <FormItem label="邮箱" label-position="left" prop="email">
            <Input class="" v-model="userInfoForm.data.email"
                   placeholder=""/>
          </FormItem>
          <FormItem label="手机号" label-position="left" prop="phone">
            <Input class="" v-model="userInfoForm.data.phone"
                   placeholder=""/>
          </FormItem>
          <FormItem label="状态" label-position="left" prop="status">
            <Input class="" v-model="userInfoForm.data.status"
                   placeholder=""/>
          </FormItem>
        </Form>
        <div class="demo-drawer-footer" style="margin: auto">
          <Button style="margin-right: 8px" @click="userInfoForm.show = false">取消</Button>
          <Button type="primary" @click="saveOrUpdateUser('userInfoForm')">提交</Button>
        </div>
      </Card>
    </Drawer>

    <IconSelect v-on:iconSelectListen="selectIcon" :clientHeight="clientHeight"
                :show="userInfoForm.iconSelect"></IconSelect>

    <Drawer v-model="userInfoForm.userView.show"
            draggable
            :closable="false"
            :mask-closable="false"
            width=1000>

      <div class="ivu-drawer-header">
        <Icon size="20" type="md-close" class="drawer-close-icon" @click="userInfoForm.userView.show=false"/>
        <div class="ivu-drawer-header-inner">详情</div>
      </div>

      <Card :bordered="false" :style="{height:(clientHeight+220)+'px'}">
        <Row type="flex" v-for="(item ,index) in userInfoGrid.columns"
             v-if="item.key"
             :key="index"
             style="height: 40px;margin-top: 5px;background: rgba(4,9,7,0.2);line-height: 35px;border-radius: 6px">
          <Col span="4">
            <div style="color: rgb(167,225,230);padding: 5px;text-align: center;font-weight: bold">
              {{item.title}}
            </div>
          </Col>
          <Col span="20">
            <div style="color: rgba(159,216,209,0.65);padding: 5px;">
              <div v-if="item.key=='icon'">
                <Icon size="30" :type="userInfoForm.userView.data[item.key||item.slot]"/>
              </div>
              <div v-else>
                            <span v-if="userInfoForm.userView.data[item.key||item.slot]" style="color: yellow">
                              {{(userInfoForm.userView.data[item.key||item.slot])}}
                            </span>
                <span v-else>
                                                                </span>
              </div>
            </div>
          </Col>
        </Row>
      </Card>
    </Drawer>
  </div>
</template>

<script>
  import IconSelect from "../../components/IconSelect";

  export default {
    components: {IconSelect},
    props: ["clientHeight"],
    data() {
      return {
        styles: {
          height: 'calc(100% - 55px)',
          overflow: 'auto',
          paddingBottom: '53px',
          position: 'static'
        },
        userInfoGrid: {
          columns: [
            {
              type: 'selection',
              width: 60,
              align: 'center',
              fixed: 'left',
            },
            {
              title: '操作',
              slot: 'action',
              width: 150,
              fixed: 'left'
            },
            {
              title: '用户名',
              key: 'username',
              width: 150,
              //fixed: 'left'
            },
            {
              title: '昵称',
              key: 'nickName',
              width: 150,
              //fixed: 'left'
            },
            {
              title: '姓名',
              key: 'name',
              width: 150,
              //fixed: 'left'
            },{
              title: '图像',
              slot: 'avatar',
              width: 100,
              //fixed: 'left'
            },
            {
              title: '生日',
              key: 'birthday',
              width: 150,
              //fixed: 'left'
            },
            {
              title: '性别',
              key: 'sex',
              width: 100,
              //fixed: 'left'
            },
            {
              title: '邮箱',
              key: 'email',
              width: 200,
              //fixed: 'left'
            },
            {
              title: '手机号',
              key: 'phone',
              width: 150,
              //fixed: 'left'
            },
            {
              title: '状态',
              key: 'status',
              width: 100,
              //fixed: 'left'
            },
            {
              title: '创建人',
              key: 'createUserId',
              width: 200,
              //fixed: 'left'
            },
            {
              title: '创建时间',
              key: 'createTime',
              width: 200,
              //fixed: 'left'
            },
            {
              title: '修改人',
              key: 'updateUserId',
              width: 200,
              //fixed: 'left'
            },
            {
              title: '修改时间',
              key: 'updateTime',
              width: 200,
              //fixed: 'left'
            }
          ],
          data: [],
          pageData: {
            total: 100,
            current: 1,
            size: 10
          }
        },
        userInfoForm: {
          title: "添加",
          iconSelect: false,
          showField: {
            id:false,
            avatar:false,
            username:false,
            password:false,
            salt:false,
            name:false,
            birthday:false,
            sex:false,
            email:false,
            phone:false,
            status:false,
            createTime:false,
            createUserId:false,
            updateTime:false,
            updateUserId:false,
            lastLoginTime:false,
            clientType:false,
          },
          select:{
            pid:[]
          },
          clearData: {
            id:'',
            avatar:'',
            username:'',
            password:'',
            salt:'',
            name:'',
            birthday:'',
            sex:'',
            email:'',
            phone:'',
            status:'',
            createTime:'',
            createUserId:'',
            updateTime:'',
            updateUserId:'',
            lastLoginTime:'',
            clientType:'',
          },
          data: {
            id:'',
            avatar:'',
            username:'',
            password:'',
            salt:'',
            name:'',
            birthday:'',
            sex:'',
            email:'',
            phone:'',
            status:'',
            createTime:'',
            createUserId:'',
            updateTime:'',
            updateUserId:'',
            lastLoginTime:'',
            clientType:'',
          },
          validate: {
            avatar:[
              {required: true, message: '请输入图像', trigger: 'blur'},
            ],
            username:[
              {required: true, message: '请输入用户名', trigger: 'blur'},
            ],
            name:[
              {required: true, message: '请输入姓名', trigger: 'blur'},
            ],
            birthday:[
              {required: true, message: '请输入生日', trigger: 'blur'},
            ],
            sex:[
              {required: true, message: '请输入性别', trigger: 'blur'},
            ],
            email:[
              {required: true, message: '请输入邮箱', trigger: 'blur'},
            ],
            phone:[
              {required: true, message: '请输入手机号', trigger: 'blur'},
            ],
            status:[
              {required: true, message: '请输入状态', trigger: 'blur'},
            ],
          },
          show: false,
          userView:{
            data: {},
            show: false
          }
        }
      }
    },
    methods: {
      checkUserName() {

      },
      showUserView(index) {
        this.userInfoForm.userView.show = true;
        this.userInfoForm.userView.data = this.userInfoGrid.data[index]
      },
      removeUser(id) {
        const that = this;
        that.$Modal.confirm({
          title: "警告",
          content: "确定要删除吗？",
          onOk: function () {
            that.$util.req("/api/userService/delUser")
              .setJsonDataStr({ids: id})
              .setSuccessfn(function (result) {
                if (result.code === 1000) {
                  that.$Notice.success({
                    title: '操作成功',
                    desc: 'User删除成功'
                  });
                  that.searchUserList()
                }else{
                  that.$Notice.error({
                    title: '操作失败',
                    desc: '${cfg.UpModuleName}删除失败'
                  });
                }
              }).go(that)
          }
        })
      },
      changeType(value, callBack) {
        callBack = callBack || function () {
        }
        if (value == "1") {
          this.userInfoForm.showField.pid = false
          this.userInfoForm.showField.viewPath = false
          this.userInfoForm.showField.groupName = false
          this.userInfoForm.data.pid = '0'
          callBack()
        } else if (value == "2" || value == "3") {
          if (value == "2") {
            this.userInfoForm.showField.groupName = true
          }
          if (value == "3") {
            this.userInfoForm.showField.groupName = false
          }
          this.userInfoForm.showField.pid = true
          this.userInfoForm.showField.viewPath = true
        }
      },
      selectIcon: function (fun) {
        fun(this)
      },
      saveOrUpdateuserInfoForm(actiontype, data) {
        if (actiontype === "add") {
          this.userInfoForm.title = "添加"
          this.userInfoForm.data = this.userInfoForm.clearData
          this.changeType(data.type)
        } else if (actiontype === "update") {
          this.userInfoForm.title = "修改"
          const that = this;
          that.$util.req("/api/userService/searchUser")
            .setJsonDataStr({id: data.id})
            .setSuccessfn(function (result) {
              let userInfo = result.data
              that.userInfoForm.data = userInfo
            })
            .go(that)
        }
        this.userInfoForm.show = true
      },
      saveOrUpdateUser(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            const that = this;
            that.$util.req("/api/userService/saveOrUpdateUser")
              .setJsonDataStr(that.userInfoForm.data)
              .setSuccessfn(function (result) {
                if (result.code === 1000) {
                  that.$Notice.success({
                    title: '操作成功',
                    desc: 'user' + that.userInfoForm.title + '成功'
                  });
                  that.searchUserList();
                  that.userInfoForm.show = false;
                }})
              .setErrorfn(function (e) {})
              .go(this)
          } else {
            //this.$Message.error('Fail!')
          }
        })
      },
      searchUserList() {
        const that = this;
        that.$util.req("/api/userService/searchUserList")
          .setJsonDataStr({
            start:that.userInfoGrid.pageData.current,
            size:that.userInfoGrid.pageData.size
          })
          .setSuccessfn(function (result) {
            if (result.success) {
              that.userInfoGrid.data = result.data.records
              that.userInfoGrid.pageData.current = result.data.current;
              that.userInfoGrid.pageData.total = result.data.total;
            }
          })
          .setErrorfn(function (e) {})
          .go(that)
      },
      changePageNum(pageNum) {
        this.userInfoGrid.pageData.current = pageNum;
        this.searchUserList()
      },
      changePageSize(pageSize) {
        this.userInfoGrid.pageData.size = pageSize;
        this.searchUserList()
      }
    }
    ,
    mounted() {
      this.searchUserList();
    }
  }
</script>

<style>

</style>



